<template>
    <div class="register container">
        <div class="info">
            <Tile :display="true"></Tile>
            <Step step="2"></Step>
            <div class="form">
                <table>
                    <tr>
                        <td class="tile"><p>用户名<span style="width: 100%;display: inline-block"></span></p></td>
                        <td class="input">
                            <label>
                                <input type="text" placeholder="请输入用户名" v-model="user">
                                <span class="fail" v-show="access_user === false">用户名已被注册</span>
                                <img v-show="access_user" class="access" src="../assets/signUp-icon-correct.png" alt="">
                            </label>
                        </td>
                    </tr>
                    <tr>
                        <td class="tile"><p>密 码<span style="width: 100%;display: inline-block"></span></p></td>
                        <td class="input">
                            <label>
                                <input type="password" placeholder="密码需为6-20位字母、数字，至少包含字母和数字" v-model="password">
                                <span class="fail" v-show="access === false">两次输入的密码不相同！</span>
                                <img v-show="access" class="access" src="../assets/signUp-icon-correct.png" alt="">
                            </label>
                        </td>
                    </tr>
                    <tr>
                        <td class="tile"><p>确认密码<span style="width: 100%;display: inline-block"></span></p></td>
                        <td class="input">
                            <label>
                                <input type="password" placeholder="请再次确认刚才输入的密码" v-model="checkPassword" @blur="check">
                            </label>
                        </td>
                    </tr>
                </table>
                <div class="btn" @click="nextStep">
                    下一步
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Tile from '../components/title'
    import Step from '../components/step'
    import {mapState} from 'vuex'

    export default {
        name: "step-two",
        components: {
            Tile,
            Step
        },
        created() {
            if (this.register.step !== 2) {
                this.$router.push('/')
            }
        },
        data() {
            return {
                access: '', //两次密码相同返回true
                access_user: '',//验证用户名是否被使用了
                user: '',
                password: '',
                checkPassword: ''
            }
        },
        methods: {
            check() {
                if (this.password === this.checkPassword) {
                    this.access = true
                } else {
                    this.access = false
                }
            },
            nextStep() {
                if (this.access === true) {
                    let data = {
                        register: {
                            step: 3,
                            phone: this.register.phone,
                            user: this.user,
                            password: this.password
                        }
                    };
                    this.$store.dispatch('register/setRegister', data);
                    this.$router.push('/sann')
                }
            }
        },
        computed: {
            ...mapState('register', ['register'])
        },
    }
</script>

<style scoped lang="stylus">
    .info
        width 800px
        margin 50px auto 0
        padding 0 50px 50px
        background-color white
        .form
            width 550px
            margin 60px auto 0
            table
                width 520px
                border-collapse separate
                border-spacing 20px 25px
                tr
                    height 50px
                    line-height 50px
                    td
                        height 50px
                        line-height 50px
                        &.tile
                            width 90px
                            font-size 16px
                            color black
                            font-weight bold
                            p
                                height 50px
                                line-height 50px
                                text-align justify
                        &.input
                            width 420px
                            position relative
                            input
                                height 50px
                                line-height 50px
                                width 100%
                                padding 0 10px
                                outline none
                                border-radius 4px
                                border 1px solid #999
                                &:focus
                                    box-shadow 0 0 5px #0089ff
                                    border-color #0089ff
                            img.access
                                display: inline-block
                                position: absolute
                                right: -25px
                                top: 17px
                            span.fail
                                position absolute
                                display inline-block
                                height 16px
                                line-height 16px
                                font-size 12px
                                color red
                                bottom -20px
                                left 0
                                animation debounce 0.1s 3
                            @keyframes debounce {
                                from {
                                    bottom -22px
                                }
                                to {
                                    bottom -20px
                                }
                            }
        .btn
            width 378px
            height 60px
            line-height 60px
            margin 40px 120px 0
            text-align center
            font-size 18px
            background-color #0089ff
            color white
            cursor pointer
            border-radius 6px
            &:hover
                background-color #33a0ff
</style>